}

@media screen and (max-width: 767px) {
  .header-list, .signup-btn {
    display: none;
  }

  .swtcher {
    display: flex;
  }

  .mob-menu {
    width: 28px;
    height: 28px;
    align-items: center;
    gap: 14px;
    margin-right: 18px;
    display: flex;
    position: relative;
  }

  .mob-menu.is-open .burger-menu {
    display: none;
  }

  .burger-menu {
    background-color: #0000;
    border: none;
  }

  .dark-theme .burger-menu #burger-svg path {
    stroke: #f3f3f3;
  }

  .dark-theme .burger-menu.is-open {
    display: none;
  }

  .burger-menu:hover #burger-svg path, .burger-menu:focus #burger-svg path {
    stroke: #4f2ee8;
  }

  .mob-container {
    z-index: 1;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 64px;
    bottom: 0;
    left: 0;
  }

  .mob-container.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mob-container.is-open .burger-menu {
    display: none;
  }

  .mob-container.is-open .close-menu {
    display: inline-block;
  }

  .mob-signup-btn {
    width: 184px;
    height: 48px;
    color: #fff;
    letter-spacing: -.56px;
    background: #4f2ee8;
    border: 1.5px solid #111;
    border-radius: 18px;
    flex-wrap: wrap;
    place-content: center;
    align-items: center;
    margin-top: 40px;
    margin-left: 20px;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    display: flex;
  }

  .arrow {
    margin-left: 90px;
  }

  .decoration {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 170px);
  }

  .close-menu {
    background-color: #0000;
    border: 0;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    line-height: 0;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: none;
    position: absolute;
  }

  .js-mob-menu.is-open .close-menu {
    display: inline-block;
  }

  .dark-theme .close-menu #close-btn path {
    stroke: #f3f3f3;
  }

  .close-menu:hover #close-btn path, .close-menu:focus #close-btn path {
    stroke: #4f2ee8;
  }
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch {
  width: 40px;
  height: 24px;
  flex-shrink: 0;
  display: inline-block;
  position: relative;
}

.switcher {
  background: url("bg.99bc1e3a.png") center no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 14px;
  display: flex;
}

input:checked + .slider {
  background: url("bg-dark.9bb764fe.png") center no-repeat;
}

input:checked + .slider:before {
  transform: translate(23px);
}

.slider {
  cursor: pointer;
  color: #4f2ee8, #dcdcdc;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background: #fff;
  border-radius: 50%;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px #2020203d;
}

